.content {
  text-align: center;
  margin-top: 100px;
  display: block;
}

.about {
  background-color: var(--dark-color);
  margin-top: 100px;
  position: absolute;
  left: 0;
  width: 100%;
  min-height: 400px;
  transform: skewY(-3deg);
  z-index: -1;
  pointer-events: none;
}

.about-items {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 180px;
}

.avatar-img img {
  border: 5px solid #f8f8f8;
  border-radius: 50%;
  width: 220px;
  height: 220px;
  margin: 0px 50px;
}

.details {
  margin-top: -50px;
}

.details p {
  display: block;
  max-width: 550px;
  max-height: 100%;
  overflow: hidden;
  text-align: justify;
  padding: 0px 15px;
  font-size: 17px;
}

@media (max-width: 450px) {
  .about {
    min-height: 480px;
  }

  .details {
    margin-top: -10px;
  }

  .details p {
    padding: 3px 20px;
    font-size: 17px;
  }
}

@media (max-width: 830px) {
  .details {
    margin-top: 0px;
  }

  .title h1 {
    padding-top: 100px;
    margin-bottom: 150px;
  }

  .about-items {
    display: flex;
    flex-direction: column;
    margin-top: -50px;
  }
}
